.search_input {
  display: flex;
  align-items: center;
  border: 1px solid black;
  height: 30px;
  width: 50%;
  padding: 10px 20px;
  border-radius: 999px;
  margin: 0 auto;
  margin-top: 10px;
}
@media only screen and (max-width: 600px) {
  .search_input {
    width:auto;
  }
}
.search_input > input {
  flex: 1;
  padding: 10px 20px;
  font-size: medium;
  border: none;
}
.search_input > input:focus {
  outline: 0;
}
.search_inputIcon {
  color: gray;
}
.search_buttons {
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.search_buttons button {
  margin: 5px;
  padding: 7px 15px;
  background-color: #f8f8f8;
  border: 1px solif white;
  text-transform: inherit;
  color: #5f6368;
}

.search_buttons button:hover {
  box-shadow: 0 3px 1px rgba(0, 0, 0, 0.1);
  background-image: linear-gradient(top, #f8f8f8, #f1f1f1);
  border-color: #f8f8f8;
  border: 1px solid #c6c6c6;
  color: #222;
}

.search_buttons_hidden {
  display: none !important;
}
