.home {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.home_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 20px;
}
.home_header a {
  margin-right: 20px;
  text-decoration: inherit;
  color: #212121;
  font-size: 15px;
  font-weight: 400;
}
.home_header a:hover {
  text-decoration: underline;
}

.home_headerRight {
  display: flex;
  align-items: center;
  min-width: 13vw;
  justify-content: space-between;
}
.home_headerRight > .MuiSvgIcon-root {
  margin-right: 20px;
}
.home_body {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.home_body > img {
  object-fit: contain;
  height: 25vw;
}
footer {
  position: fixed;
  background-color: rgb(228, 226, 226);
  bottom: 0;
  left: 0;
  right: 0;
  padding: 2px;
  font-weight: 600;
  text-align: center;
}